body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #141414;
}

a {
  text-decoration: none;
}

.homeLink,
.errorMessage,
.noneFound {
  font-family: 'Roboto', sans-serif;
}

.searchBar {
  font-family: 'Open Sans', sans-serif;
}

header {
  padding: .3em;
}

header .homeLink {
  display: inline-block;
  color: #FFF;
  font-weight: bold;
  font-size: 2em;
  text-transform: uppercase;
  padding: .1em;
}

@media screen and (min-width: 700px) {
  header .homeLink {
    font-size: 2.5em;
  }
}

@media screen and (min-width: 650px) {
  header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
    flex-direction: row;
    -ms-flex-pack: justify;
    justify-content: space-between;
    padding: 1.5%;
  }
  header .searchBar {
    width: 50%;
    max-width: 516px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
  }
}

.searchBar input[type="text"],
.searchBar .searchBtn {
  font-size: 1em;
  padding: .5em 0;
  margin: 0;
  border: 0;
}

.searchBar input[type="text"] {
  width: 100%;
  padding-left: .5em;
}

.searchBar input[type="text"]:focus {
  outline: none;
}

.searchBar .searchBtn {
  width: 10%;
  margin-left: -10%;
  color: #FFF;
}

#toTop,
.searchBtn,
.giphLink {
  background: -webkit-linear-gradient(309deg, #8eff4e, #4effab, #4ee6ff);
  background: -o-linear-gradient(309deg, #8eff4e, #4effab, #4ee6ff);
  background: linear-gradient(141deg, #8eff4e, #4effab, #4ee6ff);
}

header, ul {
  max-width: 1255px;
  width: 95%;
  margin: 0 auto;
}

.giphLink,
.giph {
  height: 14em;
}

.giphLink {
  display: block;
  float: left;
  width: 47%;
  margin: 1.5%;
}

@media screen and (min-width: 700px) {
  .giphLink {
    width: 30%;
    margin: 1.6%;
  }
}

@media screen and (min-width: 1000px) {
  .giphLink {
    width: 22%;
    margin: 1.5%;
  }
}

.giph {
  width: 100%;
}

#toTop {
  position: fixed;
  bottom: .5em;
  right: .5em;
  font-size: 2em;
  padding: .2em;
  border-radius: 50%;
  color: #fff;
}

.clearfix::after {
  content: " ";
  display: table;
  clear: both;
}

.noneFound,
.errorMessage {
  text-align: center;
  color: #FFF;
  max-width: 800px;
  margin: 0 auto;
}

.loader {
  border: 10px solid #f3f3f3;
  border-top: 10px solid #4effab;
  border-radius: 50%;
  width: 5em;
  height: 5em;
  -webkit-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
  margin: 5em auto;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}



